import React from 'react';
import PropTypes from 'prop-types';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';
import APButton from '../../common/APButton';
import APAutoTabInput from '../../common/APAutoTabInput/APAutoTabInput';
import { required } from '../../../helpers/formValidations';

import './SendReceiveTwoFactorAuthModal.css';

const baseClasses = getBEMClasses('send-receive-two-factor-auth-modal');

const SendReceiveTwoFactorAuthForm = (props, context) => {
  const { handleSubmit, submitting, error } = props;

  return (
    <form onSubmit={handleSubmit} className={baseClasses()}>
      <div className={baseClasses('text-item')}>
        {context.t('Enter the token from your Authenticator app.')}
      </div>
      <APAutoTabInput
        name="code"
        type="input"
        numberOfInputs={6}
        validate={[required]}
      />
      <div className={baseClasses('footer')}>
        <div className={error && baseClasses('text-item__failed')}>
          {error && context.t(error)}
        </div>
        <APButton
          type="submit"
          disabled={submitting}
          styleName="additive"
          customClass={baseClasses()}>
          {submitting
            ? context.t('Processing...')
            : context.t('Authenticate Transfer')}
        </APButton>
      </div>
    </form>
  );
};

SendReceiveTwoFactorAuthForm.defaultProps = {
  handleSubmit: () => {},
  submitting: false
};

SendReceiveTwoFactorAuthForm.propTypes = {
  handleSubmit: PropTypes.func
};

SendReceiveTwoFactorAuthForm.contextTypes = {
  t: PropTypes.func.isRequired
};

export default SendReceiveTwoFactorAuthForm;



