import { connect } from 'react-redux';
import ConfirmBlockTradeComponent from './ConfirmBlockTradeModalComponent';
import modalDecorator from '../../hocs/modalDecorator';
import { MODAL_TYPES } from '../../redux/actions/modalActions';
import { placeOrder } from '../../redux/actions/orderEntryActions';
import { orderFormTypes } from '../../constants/sendOrder/orderFormTypes';
import { selectedInstrumentSelector } from '../../redux/selectors/instrumentPositionSelectors';

const mapStateToProps = state => {
  const { form, product } = state;

  const selectedInstrument = selectedInstrumentSelector(state);
  const productId = form.reportBlockTrade.values.fee.ProductId;
  const feeSymbol = productId
    ? product.products.find(item => item.ProductId === productId).Product
    : '';

  return {
    formObj: form.reportBlockTrade || { values: {} },
    selectedInstrument,
    feeSymbol
  };
};

const mapDispatchToProps = dispatch => {
  return {
    confirmReport: form => dispatch(placeOrder(orderFormTypes.block, form))
  };
};

const container = connect(
  mapStateToProps,
  mapDispatchToProps
)(ConfirmBlockTradeComponent);

export default modalDecorator({
  name: MODAL_TYPES.CONFIRM_BLOCK_REPORT
})(container);



